*{
    margin:0;
    padding:0;
}
.top-nav{
    display:flex;
    justify-content:space-between;
    background:#27047a80
}
.top-nav img{
    padding:5px;
    color:#706894;
}
ul li{
    list-style-type:none;
}
ul li a{
    text-decoration: none;
    font-size:x-small;
    font-weight:bold;
    padding:10px;
    color:white;
}
nav ul{
    display:flex;
    justify-content:space-between;
}
.header{
    background:#0d0518;
    width:100%;
    height:80vh;
}
.open-source{
    display:flex;
    flex-direction:column;
    align-items:center;
    position:relative;
    top:7em;
    justify-content: center;
    color:white;
}
.open-source h3{
    color:aqua;
}
.open-source h2{
    font-weight:bold;
    letter-spacing:8px;
    font-size:60px;
}
.open-source ul{
    display:flex;
    font-size:small;
}
.open-source ul li{
    font-size:small;
    padding:5px;
    padding-top: 15px;
    position:relative;
}
.open-source ul li::after{
   position:absolute;
   content:'';
   width:2px;
   height:15px;
   margin-left:4px;
   margin-top:5px;
   background-color: white;
}
.section2{
    display:flex;
    flex-direction:column;
    align-items: center;
    justify-content: center;
    
}
.section2 h3{
    color:#1d182c;
    font-weight:bold;
    margin:15px;
    margin-top:60px;
}
.section2 div{
    margin-left:40px;
    margin-right:40px;
    text-align:center
}
.section3{
    display:flex;
    flex-direction: column;
    margin-top:50px;
    margin-left:30px;
}
.imgs1{
    display:flex;
    text-align: center;
    margin-bottom: 10px;
    margin-top:40px;
}
.section3 .maps h3{
    color:#281b4b;
    margin-bottom:20px;
}
.section3 .maps p{
    color:#312285c0
}
.section3 img{
    margin-top:40px;

}
.section3 .maps .button{
    margin-top:20px;
    text-align: center;
    background: rgb(63, 207, 207);
    width:130px;
    border-radius:30px;
    height:20px;
    padding:8px;
    color:white;
}
.section3 .maps .button p{
    color:white;
}

.section4{
    width:100%;
    height:60vh;
    margin-top:40px;
    display:flex;
    justify-content: center;
    align-items:center;
    flex-direction: column;
    background:rgb(63, 207, 207);
    color: white;
}
.section4 .p{
    font-weight:bold;
    margin-left:20px;
    margin-right:20px;
    margin-top:20px;
    font-size:medium;
    text-align: center;
}
.section4 div{
    background:white;
    border-radius:30px;
    text-align: center;
    padding:15px;
    width:170px;
    height:20px;
    color:#281b4b;
    font-size: 15px;
    margin-top:12px;

}
footer{
    background:#093a5c;
    width:100%;
    height:180px;
    
}
footer .footer2{
    width:70%;
    display:flex;
    padding:50px 50px 50px 50px;
    flex-wrap:wrap;
    justify-content: center;
}

footer .footer2 ul{
    display:flex;
    flex-wrap:wrap;
    padding-left:40px;
    padding-top:30px;
    text-align: center;
}
footer .footer2 ul li{
    padding-right: 15px;
    font-size: x-small;
    color:white;
    font-family: Arial, Helvetica, sans-serif;
}


footer .social div > ul{
    display:flex;
}

footer .base{
    background:#001829;
    width:100%;
    height:40px;
    display:flex;
    font-size: xx-small;
    justify-content: space-around;
    color:white;
    position:relative;
    bottom:0;
}
footer .base p{
    padding:10px;
}
@media(min-width:870px){
    .open-source h2 {

        font-size:100px;
    }
    .section3 .row{
        display:flex;
        flex-direction: row;
        justify-content: space-around;
        margin-top:50px;
    }
    .section3 .maps h3 p{
        text-align:center
    }

    .section3 .row .imgs1{
        flex:2
    }
    .section3 .row .maps{
        flex:3
    }
    .section3{
        max-width:80%;
        margin-left:10%;
        margin-right:10%;
    }
    .section3 .row .order1{
        order:2;
    }
    .section3 .row .order2{
        order:1;
    }
    .section3 .row .imgs1{
        padding-left:80px;
    }

    
}
@media only screen and (max-width:560px){
     .headies{
        font-size:8px;
    }
    .section2 h3{
        text-align: center;
    }
}